                            </RouterLink>
                            <!-- <a href="javascript:;" id="ancAddGroupMyDashboard" class="p-action-btn text-white d-none" >Add Group</a> -->
                            <a href="javascript:;" id="ancAddGroupMyDashboard" v-if="IsEdit" class="p-action-btn text-white" title="@Resources.AddNew @Resources.Group"><em class="fa fa-plus" aria-hidden="true"></em><br>{{  $t('AddNewGroup') }}</a>
                            <a href="javascript:;" id="ancSaveMyDashboard" v-if="IsEdit"  class="p-action-btn text-white" title="@Resources.Save"><em class="fa fa-save" aria-hidden="true"></em><br>{{ $t('Save') }}</a>
                            <a href="javascript:;" id="ancCancelMyDashboard" @click="CancelEditDashBoardData()"  v-if="IsEdit" class="p-action-btn text-white" title="@Resources.Cancel"><em class="fa fa-times" aria-hidden="true"></em><br> {{ $t('Cancel') }}</a>
                            <a href="javascript:;" @click="EditDashBoardData()" v-if="IsEdit==false" class="p-action-btn text-white" checkPrivilege="Yes">
                                <em class="fa fa-pencil"></em><br />{{ $t("Edit") }}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <!-- end page heading -->
    <section class="main-content">
        <div class="container-fluid p-0">
            <div class="border p-3 form-minheight d-flex iPadcontroldiv">
                <div class="w-100 left side" :class="{'w-75': customizeShortcuts}">
                    <div class="row">
                        <div class="w-100 clsdashboardgroup p-0" v-for="(parentItem, parentIndex) in MenuDataGroupBy">
                            <div class="clearfix"></div>
                            <div class="col-lg-12 mb-0 float-left px-3">
                                <div class="heading-border">
                                    <h5 class="h5">
                                        <span class="spngropuname">
                                            {{parentItem.GroupName}}
                                            <a title="@Resources.Edit" href="javascript:;" class="editgroupname d-none"><em class="fa fa-pencil pl-2 text-success"></em></a>
                                        </span>
                                        <input type="text" maxlength="100" class="form-control mb-1 txtgropuname" :value="parentItem.GroupName" style="display:none;" />

                                        <a class="delete-group d-none" v-if="parentIndex>0" data-id="@group.First().FAV_DASHBOARD_GROUP_ID" title="@Resources.Delete">
                                            <em class="text-secondary fa fa-times-circle"></em>
                                        </a>

                                    </h5>
                                </div>

                            </div>
                            <div class="clearfix"></div>
                            <ul class="shortcuts-menu pb-3 connectedSortable">
                                <li class="position-relative" v-for="item in parentItem.GroupData">
                                    <a href="javascript:;" class="delete-dashboard d-none" title="@Resources.Delete"><em class="text-secondary fa fa-times-circle"></em></a>
                                    <a onmousedown="return false;" data-id="@item.PRIVILEGE_ID" data-fav_id="@item.FAV_DASHBOARD_ID" data-order="@item.DISPLAY_ORDER" data-displayName="@item.DISPLAY_NAME" data-toggle="tooltip" title="@Resources.ResourceManager.GetString(item.DISPLAY_NAME)" data-placement="bottom" class="short-menu-item d-none">
                                        <span class="left-icon-m">
                                            <em :class="'fa '+item.PRIVILEGE_ICON"></em>
                                        </span>
                                        <span class="short-item-name">{{ $t(item.DISPLAY_NAME) }}</span>
                                    </a>
                                    <RouterLink :to="`/${item.MODULE_NAME}/${item.PRIVILEGE_NAME}?reqfrom=${item.MODULE_MASTER_ID}`" class="short-menu-item" checkPrivilege="Yes">
                                        <span class="left-icon-m">
                                            <em :class="'fa '+item.PRIVILEGE_ICON"></em>
                                        </span>
                                        <span class="short-item-name">{{ $t(item.DISPLAY_NAME) }}</span>
                                    </RouterLink>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div v-if="customizeShortcuts" class="w-25 rightside">
                    <div class="left-menu-tab ml-3 mr-3" style="max-width:96%;">
                        <div class="col-md-12 p-0 ">
                            <div class="theme-primary partition">
                                <span class="p-name text-white">Customize Shortcuts</span>
                            </div>
                            <div class="mb-0 d-lg-block p-2 border">
                                <div class="input-group customsearchbox">
                                    <input type="text" maxlength="50" v-on:keydown="ListFiltering" v-on:keyup="ListFiltering" v-on:mouseup="SelectAllSearch" v-on:mouseleave="SelectAllSearch" id="searchinput"
                                        v-model="MainSearchmodel" placeholder="Search by Name"
                                        class="form-control searchcustomfilter border-right-0" autocomplete="off">
                                    <div class="input-group-append"><span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span></div>
                                </div>
                            </div>
                            <!-- <draggable v-model="list" @end="onDragEnd">
                                <div v-for="(item, index) in list" :key="index" class="draggable-item">
                                    {{ item }}
                                </div>
                            </draggable> -->
                            <!-- <div class="grid-container">
      <draggable v-model="listA" class="grid-item" group="lists" @end="onDragEnd">
        <div v-for="(item, index) in listA" :key="index" class="list-item">
          {{ item }}
        </div>
      </draggable>

      <draggable v-model="listB" class="grid-item" group="lists" @end="onDragEnd">
        <div v-for="(item, index) in listB" :key="index" class="list-item">
          {{ item }}
        </div>
      </draggable>
    </div> -->
                        </div>
                        <div class="card" style="border-bottom:1px solid #929292;"
                            v-for="(name, index) in submodulecodename" :key="index">
                            <div class="card-header pl-0" role="tab" :id="'headingOnee' + index">
                                <h5 class="mb-0"><a data-toggle="collapse" data-parent="#accordionEx"
                                        href="javascript:;" :aria-expanded="index == 0 ? true : false" :data-target="'#collapseOne' + index"
                                        :aria-controls="'collapseOne' + index" :class="index == 0 ? '' : 'collapsed'"><em
                                            class="fa fa fa-file-text-o" aria-hidden="true"></em><span
                                            class="font-weight-bold">
                                            {{ GetLocalizedValue(name.SubModule) }}</span><em class="fa fa-angle-down rotate-icon"
                                            aria-hidden="true"></em></a><a class="tab-Search-btn"><em class="fa fa-search"
                                            aria-hidden="true"></em></a>
                                </h5>
                            </div>
                        </div>
                        <div class="w-100 p-3 bg-grey common-height">             
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>
<style>
.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.grid-item {
  border: 1px solid #ddd;
  padding: 10px;
}

.list-item {
  background-color: #f9f9f9;
  margin: 5px;
  padding: 10px;
  cursor: grab;
}
</style>
<script>
import draggable from "vuedraggable";
    export default { 
        components: {
        draggable,
    },
    data() {
        return {
            // list: ['Item 1', 'Item 2', 'Item 3', 'Item 4'],
            listA: ['Item 1', 'Item 2', 'Item 3'],
      listB: ['Item 4', 'Item 5', 'Item 6'],
            GetUserInfo : storeToRefs(useloggedUserInfo()),
            customizeShortcuts: false,
            MenuData: [],
            MenuDataList: [],
            MaxLoop: 5,
            ModuleName: '',
            MenuDataGroupBy: [],
            MenuCountmodule: 0,
            TotalCount: 0,
            TotalMenuCount: 0,
            Languages: [],
            SelectedLanguage: 'English',
            SelectedMenuType: 'Top',
            IsEdit: false,
        }
    },
    created: function () { 
        const { SetModuleId } = moduleStore();
        SetModuleId(0);
        this.GetFavDashBoardData();
    },
    methods: {  
    //     onDragEnd() {
    //   // Handle drag end logic if needed
    //   console.log('List after drag:', this.list);
    // },
    onDragEnd() {
      console.log('Drag ended');
    },
        CancelEditDashBoardData() {
            this.IsEdit = false;
            this.customizeShortcuts = false;
        }, 
        EditDashBoardData() {
            this.IsEdit = true;
            this.customizeShortcuts = true;
            $(".leftsideicon").find(".txtgropuname").closest(".heading-border").addClass("editgroup");
            $(".rightsideicon").removeClass("d-none");
            $("#ancSaveMyDashboard, #ancCancelMyDashboard").removeClass("d-none");             
            $('.spngropuname').attr("contenteditable", true).hide();
            $('.txtgropuname').show();
            $('.spngropuname').off('keypress').on("keypress", function () {
                return (this.innerText.length <= 100)
            });
            $(".left-icon-m").addClass("cursor-move").off('click').on('click', function () {
                return false;
            });
            $('.shortcuts-menu a:not(.delete-dashboard)').off('click').on('click', function () {
                return false;
            });
            $('.shortcuts-menu a:not(.delete-group)').off('click').on('click', function () {
                return false;
            });
        },
        async GetFavDashBoardData() {
            var vm = this;
            //const headers = {                    
            //    'Authorization': 'Bearer ' + this.GetUserInfo.Token
            //};                
            DataService.GetFavDashboard()
                .then((response) => {
                
                if (response.data.status == "Success") {
                    vm.MenuData = response.data.result.DATA;
                    var MenuGroupData = [];
                    if (vm.MenuData != null && vm.MenuData.length > 0) {
                        var groupId = vm.MenuData[0].FAV_DASHBOARD_GROUP_ID;
                        $.each(vm.MenuData, $.proxy(function (index, item) {
                            groupId = item.FAV_DASHBOARD_GROUP_ID;
                            var chkGroup = $.grep(MenuGroupData, function (element, index) {
                                return element.GroupId == groupId;
                            });
                            //debugger;
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(vm.MenuData, function (element, index) {
                                    return element.FAV_DASHBOARD_GROUP_ID == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        GroupId: groupId,
                                        GroupName: returnedData[0].GROUP_NAME,
                                        GroupData: returnedData
                                    };
                                    MenuGroupData.push(objGroup);
                                }
                            }
                        }, this));
                        this.MenuDataGroupBy = MenuGroupData;
                    }
                    //vm.$toast.open('Data get successfully');
                    // this.$toast.success('Data get successfully');
                }
            });
        }       
    }
} 
   
</script>

